body {
  font-family: "Poppins", sans-serif;
}

.header {
  background-color: #fb8c8c;
  width: 100%;
  position: fixed;
  top: 0;
}

.nav-bar {
  max-width: 90%;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.mobile-menu {
  display: none;
}

.mobile-menu-slide {
  display: none;
}
.mobile-menu-slide .mobile-menu-slide__links {
  display: none;
}

.nav-bar-images img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.links {
  display: flex;
  justify-content: space-around;
  column-gap: 15px;
}
.links li {
  list-style: none;
}
.links li a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 700;
  background-color: #f8adad;
  color: black;
  padding: 10px;
  border-radius: 15px;
  transition: all 0.3s;
}
.links li a:hover {
  background-color: #f57777;
}

.start {
  width: 100%;
  height: 100vh;
  margin-top: -60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.start picture img {
  background-size: contain;
}

.presentation {
  display: flex;
  width: 50%;
  column-gap: 50px;
}

.about {
  width: 100%;
  height: 100vh;
  margin-top: -60px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.about h2 {
  font-size: 50px;
}

.backend-about-me {
  display: flex;
  column-gap: 50px;
  margin: 0 300px;
  align-items: center;
}
.backend-about-me .backend-text {
  font-size: 20px;
  font-weight: 400;
}

.learning {
  width: 100%;
  height: 100vh;
  margin-top: -60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.learning h2 {
  font-size: 40px;
  margin-bottom: 30px;
  margin-top: -70px;
}

.tech-section h3 {
  font-size: 25px;
  text-align: center;
  margin-bottom: 40px;
}

.tech-images {
  display: flex;
  column-gap: 50px;
}
.tech-images li {
  list-style: none;
}

.social-media {
  width: 100%;
  height: 100vh;
  margin-top: -60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.social-media ul {
  display: flex;
  column-gap: 50px;
}
.social-media ul li {
  list-style: none;
  transition: all 0.3s;
  border-radius: 30px;
  padding: 5px;
}
.social-media ul li:hover {
  background-color: #878787;
  border-radius: 30px;
  padding: 5px;
}

.footer {
  background-color: #000000;
}
.footer menu li > a {
  background-color: #000;
  color: #fff;
}
.footer menu li > a:hover {
  background-color: #f57777;
  color: #000;
}

@media (min-width: 360px) and (max-width: 768px) {
  .nav-bar {
    justify-content: space-between;
  }
  .nav-bar .mobile-menu {
    display: block;
    border-radius: 100%;
  }
  .nav-bar a img {
    border-radius: 10%;
  }
  .nav-bar menu {
    display: none;
  }
}
@media (min-width: 360px) and (max-width: 768px) {
  .mobile-menu-slide {
    display: none;
    position: absolute;
    top: 100px;
    left: 0px;
    background-color: #955d5d;
    width: 100%;
    height: 95dvh;
  }
  .mobile-menu-slide .mobile-menu-slide__links {
    display: block;
  }
}